<template>
  <div class="page-metarial">
    <h6 class="page-title"></h6>
    <div class="usage">
      :
      <p>1. </p>
      <p>2. URL</p>
      <p>3. URL</p>
      <p class="warning-text">: !</p>
    </div>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="default">
        <gallery-list group="default" :pageNum="1" :pageSize="15"></gallery-list>
      </el-tab-pane>

      <el-tab-pane label="" name="menu">
        <gallery-list group="menu" :pageNum="1" :pageSize="15"></gallery-list>
      </el-tab-pane>

      <el-tab-pane label="" name="avatar">
        <gallery-list group="avatar" :pageNum="1" :pageSize="15"></gallery-list>
      </el-tab-pane>
    </el-tabs>

  </div>
</template>
<script>
// import { mapActions } from "vuex";
import galleryList from './gallery-list.vue'

export default {
  name: "main-gallery",
  data() {
    return {
      activeName: 'default'
    }
  },
  components: { galleryList },
  methods: {
    handleClick() {}
  }
};
</script>

<style lang="less" scoped>
.page-metarial {
  .el-tabs__content {
    position: absolute !important;
    overflow: scroll;
  }

  .page-title {
    margin-bottom: 10px;
  }

  .usage {
    color: #999;
    font-size: 14px;
    line-height: 160%;
    padding-bottom: 20px;
    p {
      padding: 0;
      margin: 0;
    }
    .warning-text {
      font-size: 12px;
      color: #f55b5b;
    }
  }
}

</style>




